.map{
    height: 500px;
    background-color: white;
    padding: 0.5rem;
    border-radius: 6px;
    margin-top: 16px;
    box-shadow: 0px 2px 1px -1px rgba(0,0,0,0.2), 0px 1px 1px 0px rgba(0,0,0,0.14), 0px 1px 3px 0px rgba(0,0,0,0.12);
}




.map .leaflet-container{
    height: 100%;
}

.info-flag img{
    width: 100px;
    border-radius: 5px;
}

.info-flag {
    height: 80px;
    width: 100%;
    background-size: cover;
    border-radius: 8px;
}

.info-name{
    font-size: 20px;
    font-weight: bold;
    color: #555;
}

.info-confirmed,
.info-recovered,
.info-deaths {
    font-size: 16px;
    margin-top: 5px;
}

.info-container{
    width: 150px;
}